body, html, main {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%;
}

div#root {
  height: 100%;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

main header{
  height:10%;
}

main section {
  height: 90%;
}

@font-face {
  font-family: 'FredokaOne';
  src: url(/ez-pyramidy/static/media/FredokaOne-Regular.5165c0fa.ttf);
}

.App, main {
  text-align: center;
  background-color: #177cff;
  font-family: 'FredokaOne', serif;
  font-size: 1.2em;
}

main {
  padding: 2%;
  box-sizing: border-box;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.App {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

div.pyramidy-main {
  display: grid;
  height: 100%;
  width: 100%;
}

div.pyramidy-categorie {
  margin: 5% 2.5%;
  border: 2px solid #fcff33;
  color: #fcff33;
  cursor: pointer;
  vertical-align: middle;
  display: flex;
  justify-content: center;
  align-items: center;
  padding:5px;
}

div.pyramidy-categorie.pyramidy-categorie-disabled {
  opacity: 0.5;
  color: #282c34;
  pointer-events: none;
  cursor: auto;
}

div.overlay {
  position: fixed;
  padding: 0;
  margin: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: #177cff;
  font-family: 'FredokaOne', serif;
  color: #fcff33;
  vertical-align: middle;
  display: flex;
  justify-content: center;
  align-items: center;
  
}

div.overlay p{
  vertical-align: middle;
  font-size: 32pt; 
}

button {
  background-color: #fcff33;
  color: #177cff;
  border-radius: 10px;
  font-size: 1em;
  font-family: 'FredokaOne', 'serif';
}

.pyramidy-timer {
  position: fixed;
  padding: 0;
  margin: 0;
  top:10%;
  left:49%;
  width:50px;
  height:50px;
  z-index:11;
  font-family: 'FredokaOne';
  background-color: #177cff;
  color: crimson;
}

header h4, h3 {
  margin: 2px;
}

h4 {
  font-size: 12pt;
}

h3 {
  font-size: 14pt;
}
